<template>
  <div class="doctorlist">
    <v-dialog v-model="dialog" width="70%">
      <template v-slot:activator="{ on }">
        <v-icon color="brown" dark large v-on="on" @click="enterProcess">mdi-cog-box</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="12">
                <v-text-field
                  prepend-icon="mdi-doctor"
                  label=" or "
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-btn class="primary" block @click="addOK" v-show="!editMode"></v-btn>
                <v-btn class="success" block @click="editOK" v-show="editMode"></v-btn>
                <v-btn class="error mt-1" block @click="cancelEditMode" v-show="editMode"></v-btn>
              </v-col>
            </v-row>
          </v-form>

          <v-card class="my-2" v-show="!editMode" style="overflow:auto">
            <v-card-text>
              <v-sheet color="green lighten-3" outlined class="text-center pa-2">
                <h3 class="white--text">()</h3>
              </v-sheet>
              <v-list height="50vh">
                <v-list-item-group>
                  <v-list-item v-for="(item, i) in filterItems" :key="i">
                    <v-list-item-content @click.stop="enterEditMode(item)">
                      <v-list-item-title v-text="item.text"></v-list-item-title>
                    </v-list-item-content>
                    <v-icon color="red" @click.stop="del(item)">mdi-close</v-icon>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-card-text>
          </v-card>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
        </v-card-actions>
        <!-- <pre>{{items}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      items:[],
      list: {
        name: ""
      }, //
      dialog: false,
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || ""],
      editMode: false,
    };
  },

  computed: {
    filterItems() {
      if (this.items.length > 0) {
        var str = "";
        return this.items.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(this.list.name.toUpperCase())) {
            return item;
          }
        });
      }
    },
  },

  mounted(){
    this.getAllData();
  },

  methods: {
    getAllData() {
      //
      //this.$store.commit("setLoadingOn"); //on
      //
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/doctor")
        .then(rs => {
          var newData = rs.data.map(item=>{
            return {
              snkey: item.snkey,
              name: item.name,
              text: item.name,
            }
          });
          // this.$store.commit("setLoadingOff"); //off
          this.items = newData.sort(function(a,b){
            return parseInt(a.snkey) < parseInt(b.snkey) ? 1 : -1;
          })
        });
    },
    enterProcess() {
      //,
      this.title = "";
      this.titleStyle = "font-weight-black primary lighten-2";
    },
    addOK() {
      //
      if (this.$refs.form.validate()) {
        //

        var repeat = this.items.find(item => {
          return (this.list.name == item.name)
        });

        if (repeat != undefined) {
          var pop = { msg: "!!", type: true, theme: "warning" };
          this.$store.commit("snackbar", pop);
          return;
        }

        this.$store.commit("getTime"); //
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //,,

        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/doctor",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$emit("")
              // this.$store.dispatch("getItems", "hospital"); //
              this.getAllData();
              this.list.name = "";
              this.$refs.form.resetValidation();
              this.$emit("getSelect");
            }
          });
      }
    },
    del(delData) {
      //
      this.$confirm("?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'doctor',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/doctor",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
                this.$emit("getSelect");
                // this.$store.dispatch("getItems", "hospital"); //
              }
            });
        }
      });
    },
    enterEditMode(item) {
      //
      this.list.snkey = item.snkey;
      this.list.name = item.text;
      this.list.edit_man = '';
      this.editMode = true;
    },
    editOK() {
      //
      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/doctor",
          qs.stringify(this.list)
        )
        .then(rs => {
          // console.log(rs.data)
          if (rs.data.state == 1) {
            var pop = { msg: "", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.getAllData();
            this.$emit("getSelect");
            // this.$store.dispatch("getItems", "branch"); //
            this.list = {
              name: ""
            };
            this.$refs.form.resetValidation();
            this.editMode = false;
          }
        });
    },
    cancelEditMode() {
      //
      this.editMode = false;
      this.list = {
        name: ""
      };
      this.$refs.form.resetValidation();
    }
  }
};
</script>

<style lang="scss">
