<template>
  <div class="MedicalRecordAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>
          {{ title }}
          <v-spacer></v-spacer>
          <v-btn class="primary" @click.stop="insertLast"
            ></v-btn
          >
        </v-card-title>

        <v-card-text>
          <v-alert text color="primary" class="mt-2" outlined>
            
            <v-row>
              <v-col cols="12" md="2">
                <v-text-field
                  label=""
                  outlined
                  readonly
                  hide-details
                  :value="this.$store.state.uData.bed_name"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="2">
                <v-text-field
                  label=""
                  outlined
                  readonly
                  hide-details
                  :value="this.$store.state.uData.name"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="2">
                <v-text-field
                  label=""
                  outlined
                  readonly
                  hide-details
                  :value="this.$store.state.uData.p_code"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  label=""
                  outlined
                  readonly
                  hide-details
                  :value="diagnosis"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-alert>
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="12" md="2">
                <v-text-field
                  label=""
                  type="date"
                  v-model="list.date"
                  :rules="emptyRules"
                ></v-text-field>
                <!-- <datepicker :label="''" v-model="list.date" :rules="emptyRules"></datepicker> -->
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="outpatientItems"
                  label=""
                  v-model="list.outpatient"
                ></v-select>
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="timeItems"
                  label=""
                  v-model="list.time"
                ></v-select>
              </v-col>
              <v-col cols="12" md="3" class="d-flex align-center">
                <v-autocomplete
                  label=""
                  :items="hospitalItems"
                  v-model="list.hospitaldata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="''"
                  :dataSource="'hospital'"
                  :defaultData="list.hospital_name"
                  @returnFn="getHospital"
                ></Selecter>-->
                <HospitalList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></HospitalList>
              </v-col>
              <v-col cols="12" md="3" class="d-flex align-center">
                <v-autocomplete
                  label=""
                  :items="branchItems"
                  v-model="list.branchdata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="''"
                  :dataSource="'branch'"
                  :defaultData="list.branch_name"
                  @returnFn="getBranch"
                ></Selecter>-->
                <BranchList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></BranchList>
              </v-col>
              <v-col cols="12" md="2" class="d-flex align-center">
                <v-autocomplete
                  label=""
                  :items="doctorItems"
                  v-model="list.doctordata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="''"
                  :dataSource="'doctor'"
                  :defaultData="list.doctor_name"
                  @returnFn="getDoctor"
                ></Selecter>-->
                <DoctorList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></DoctorList>
              </v-col>
              <v-col cols="12" md="7" class="d-flex align-center">
                <v-autocomplete
                  label=""
                  :items="main_cause_of_treatmentItems"
                  v-model="list.main_cause_of_treatmentdata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="''"
                  :dataSource="'main_cause_of_treatment'"
                  :defaultData="list.main_cause_of_treatment_name"
                  @returnFn="getMainCauseOfTreatment"
                ></Selecter>-->
                <MainCauseOfTreatmentList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></MainCauseOfTreatmentList>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field
                  label=""
                  type="date"
                  v-model="list.back_date"
                ></v-text-field>
                <!-- <datepicker :label="''" v-model="list.back_date" :rules="emptyRules"></datepicker> -->
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="carItems"
                  label=""
                  v-model="list.car"
                ></v-select>
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="accompanyItems"
                  label=""
                  v-model="list.accompany"
                ></v-select>
              </v-col>

              <v-col cols="12" md="3" class="d-flex">
                <v-combobox
                  :items="carryitemsItems"
                  multiple
                  label=""
                  v-model="list.carryArray"
                ></v-combobox>
              </v-col>

              <v-col cols="12">
                <v-alert color="primary" outlined>
                  <h4>(-)</h4>
                  <v-chip
                    class="ma-2"
                    color="primary"
                    v-for="(plist, index) in matchPipelineItems"
                    :key="index"
                  >
                    {{ plist.name }} - {{ plist.end_date }}
                    <!-- <v-icon color="red" @click="delPipeline(index)">mdi-close</v-icon> -->
                  </v-chip>
                  <!-- <h4></h4>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-autocomplete :items="pipelineItems" label="" v-model="pipelineArray.name" return-object></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="3">
                      <v-text-field label="" type="date" v-model="pipelineArray.date"></v-text-field>
                    </v-col>
                    <v-col cols="12" md="3">
                      <v-btn class="primary" block @click.stop="addPipeline"></v-btn>
                    </v-col>
                    <v-col cols="12">
                      <h4>(-)</h4>
                      <v-chip
                        class="ma-2"
                        color="primary"
                        v-for="(plist,index) in list.pipeline"
                        :key="index"
                      >
                        {{plist.name}} - {{plist.date}}
                        <v-icon color="red" @click="delPipeline(index)">mdi-close</v-icon>
                      </v-chip>
                    </v-col>
                  </v-row>-->
                </v-alert>
              </v-col>

              <v-col cols="12">
                <v-textarea
                  label=""
                  v-model="list.description"
                ></v-textarea>
              </v-col>

              <v-col cols="12">
                <v-textarea
                  label=""
                  v-model="list.response"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-checkbox
            class="mb-3 mr-2 pa-2 error white-font"
            label=""
            color="white"
            v-model="accountingCheck"
            value="true"
            hide-details
            v-if="processType == 'edit'"
          ></v-checkbox>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType == 'add'"
            :loading="loading"
            :disabled="loading"
            ></v-btn
          >
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType == 'edit'"
            :loading="loading"
            :disabled="loading"
            ></v-btn
          >
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import datepicker from "@/components/DatePicker";
import HospitalList from "@/components/HospitalList";
import BranchList from "@/components/BranchList";
import DoctorList from "@/components/DoctorList";
import MainCauseOfTreatmentList from "@/components/MainCauseOfTreatmentList";
import dayjs from "dayjs";

// import Selecter from "@/components/Selecter";

export default {
  components: {
    datepicker,
    HospitalList,
    BranchList,
    DoctorList,
    MainCauseOfTreatmentList,
  },

  props: ["medicalRecordItems"],

  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      processType: "", //addedit
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || ""],
      accountingCheck: "", //

      timeItems: ["", "", ""], //
      outpatientItems: ["", "", "", "", ""], //
      carItems: [
        "",
        "()",
        "()",
        "",
        "",
        "",
        "",
        "",
        "",
      ], //
      accompanyItems: ["", "", "", "", ""], //
      pipelineItems: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      carryitemsItems: ["", "", "", "TB"],
      //
      pipelineArray: {
        name: "",
        date: "",
      },
      loading: false,
      hospitalItems: [],
      branchItems: [],
      doctorItems: [],
      main_cause_of_treatmentItems: [],

      matchPipelineItems: [], //
    };
  },

  computed: {
    diagnosis() {
      if (this.$store.state.uData.diagnosis != undefined) {
        return Object.values(this.$store.state.uData.diagnosis);
      }
    },
  },

  mounted() {
    //function (showFromParent) id
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });

    this.getSelect();
  },

  methods: {
    //
    getSelect() {
      this.getSelectItems("hospital");
      this.getSelectItems("branch");
      this.getSelectItems("doctor");
      this.getSelectItems("main_cause_of_treatment");
    },
    insertLast() {
      var lastData = this.medicalRecordItems.sort(function (a, b) {
        return a["date"] < b["date"] ? 1 : -1;
      });
      this.list = { ...lastData[0] };
      //select
      var checkCol = [
        "hospital",
        "branch",
        "doctor",
        "main_cause_of_treatment",
      ];
      checkCol.forEach((i) => {
        this[i + "Items"].find((cItem) => {
          if (cItem.snkey == this.list[i + "_snkey"]) {
            this.$set(this.list, i + "data", cItem);
          }
        });
      });
      this.list.date = dayjs().format("YYYY-MM-DD");
    },
    mapData(database, data) {
      this[database + "Items"] = data.map((i) => {
        return {
          snkey: i.snkey,
          text: i.name,
        };
      });
    },
    //
    getSelectItems(database) {
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "general/getall/" + this.$store.state.databaseName + "/" + database
        )
        .then((rs) => {
          // this[database + "Items"] = rs.data;
          this.mapData(database, rs.data);
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    getPipeline() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/pipeline_used",
          qs.stringify(postData)
        )
        .then((rs) => {
          var pipelineData = rs.data.map((i) => {
            var newItem = JSON.parse(i.datalist);
            newItem["snkey"] = i.snkey;
            newItem["create_man"] = i.create_man;
            newItem["edit_man"] = i.edit_man;
            return newItem;
          });
          this.matchPipelineItems = pipelineData.filter((i) => {
            return (
              dayjs(i.end_date).format("YYYY-MM-DD") >
              dayjs().format("YYYY-MM-DD")
            );
          });

          // this.$store.commit("setLoadingOff"); //off
        });
    },
    //
    delPipeline(index) {
      this.list.pipeline.splice(index, 1);
    },
    //
    addPipeline() {
      if (
        this.pipelineArray.name.length > 0 &&
        this.pipelineArray.date.length
      ) {
        this.list.pipeline.push({ ...this.pipelineArray });
      }
    },
    //,
    addProcess() {
      this.processType = "add";
      this.title = "";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        pipeline: [],
        car: "",
        accompany: "",
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
      this.getPipeline();
    },
    //,
    editProcess(item) {
      console.log(item);
      this.processType = "edit";
      this.title = "";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //
      this.list = { ...item }; //
      // if (this.list.back_date == "0000-00-00") {
      //   this.list.back_date = "";
      // }

      //select
      var checkCol = [
        "hospital",
        "branch",
        "doctor",
        "main_cause_of_treatment",
      ];
      checkCol.forEach((i) => {
        this[i + "Items"].find((cItem) => {
          if (cItem.snkey == this.list[i + "_snkey"]) {
            this.$set(this.list, i + "data", cItem);
          }
        });
      });

      // this.$set(this.list, "pipelineArray", this.list.pipeline.split(","));
      this.$set(this.list, "carryArray", this.list.carryitems.split(","));
      this.getPipeline();
    },
    addOK() {
      //
      if (this.list.car.includes("") && this.list.accompany == "") {
        var pop = {
          msg: ",",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
        return false;
      }
      //end

      //
      if (this.$refs.form.validate()) {
        //
        var checkCol = [
          "hospital",
          "branch",
          "doctor",
          "main_cause_of_treatment",
        ];
        var noMatch = checkCol.filter((i) => {
          if (this.list[i + "data"] == undefined) {
            return i;
          } else {
            if (this.list[i + "data"].snkey == undefined) {
              return i;
            }
          }
        });
        if (noMatch.length > 0) {
          var pop = {
            msg: ",!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
          return false;
        }
        // end

        //
        this.$store.commit("getTime"); //
        var postData = {
          date: this.list.date,
          user_snkey: this.$store.state.uData.snkey,
          hospital_snkey: this.list.hospitaldata.snkey,
          branch_snkey: this.list.branchdata.snkey,
          doctor_snkey: this.list.doctordata.snkey,
          main_cause_of_treatment_snkey: this.list.main_cause_of_treatmentdata
            .snkey,
          time: this.list.time,
          outpatient: this.list.outpatient,
          description: this.list.description,
          response: this.list.response,
          back_date: this.list.back_date,
          car: this.list.car,
          accompany: this.list.accompany,
          create_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //,,
        };

        if (this.list.pipeline != undefined) {
          postData["pipeline"] = JSON.stringify(this.list.pipeline);
        }
        if (this.list.carryArray != undefined) {
          postData["carryitems"] = this.list.carryArray.toString(); //combobox ->string
        }

        this.loading = true;
        //this.$store.commit("setLoadingOn"); //on
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/medical_record",
            qs.stringify(postData)
          )
          .then((rs) => {
            console.log(rs.data);
            if (rs.data.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;
              this.$emit("getMedicalData");
              // this.$store.commit("setLoadingOff"); //off
              this.loading = false;
            }
          });

        //
        this.accountingFeesCheck();
        // var carFee = { ...this.list }; //
        // //-> :
        // if (carFee.car != undefined && carFee.hospitaldata != undefined) {
        //   console.log("run car");
        //   //this.$store.commit("setLoadingOn"); //on
        //   //
        //   this.axios
        //     .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        //     .then((rs) => {
        //       // console.log(carFee);
        //       var feeData = [];
        //       if (carFee.car.includes("()")) {
        //         console.log("");
        //         if (
        //           this.list.main_cause_of_treatmentdata.text == "" ||
        //           this.list.main_cause_of_treatmentdata.text == ""
        //         ) {
        //           feeData = rs.data.filter((i) => {
        //             return i.name.includes(
        //               carFee.hospitaldata.text + "()"
        //             );
        //           });
        //         } else {
        //           feeData = rs.data.filter((i) => {
        //             return i.name.includes(carFee.hospitaldata.text + "()");
        //           });
        //         }
        //         console.log(feeData);
        //       }

        //       if (
        //         carFee.car.includes("") ||
        //         carFee.car.includes("")
        //       ) {
        //         console.log(carFee.car);
        //         feeData = rs.data.filter((i) => {
        //           return i.name.includes(carFee.car);
        //         });
        //         console.log(feeData);
        //       }

        //       if (carFee.car.includes("")) {
        //         console.log("");
        //         feeData = rs.data.filter((i) => {
        //           return i.name.includes(carFee.car);
        //         });
        //         console.log(feeData);
        //       }

        //       // (>0 )
        //       if (feeData.length > 0) {
        //         var postData = {
        //           user_snkey: this.$store.state.uData.snkey,
        //           fee_snkey: feeData[0].snkey,
        //           date: carFee.date,
        //           start_day: carFee.date,
        //           end_day: carFee.date,
        //           quantity: 1,
        //           price: feeData[0].price,
        //           note: "(A)",
        //           create_man:
        //             this.$store.state.pData.username +
        //             "(" +
        //             this.$store.state.time +
        //             ")", //,,
        //         };

        //         this.axios
        //           .post(
        //             "general/add/" +
        //               this.$store.state.databaseName +
        //               "/accounting",
        //             qs.stringify(postData)
        //           )
        //           .then((rs) => {
        //             if (rs.data.state == 1) {
        //               var pop = {
        //                 msg: ";[]",
        //                 type: true,
        //                 theme: "success",
        //               };
        //               this.$store.commit("snackbar", pop);
        //               // this.$store.commit("setLoadingOff"); //off
        //             }
        //           });
        //       }
        //     });
        // }
      } else {
        var pop = {
          msg: ",",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //
    editOK() {
      //
      if (this.list.car.includes("") && this.list.accompany == "") {
        var pop = {
          msg: ",",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
        return false;
      }
      //end

      if (this.$refs.form.validate()) {
        //
        var checkCol = [
          "hospital",
          "branch",
          "doctor",
          "main_cause_of_treatment",
        ];
        var noMatch = checkCol.filter((i) => {
          if (this.list[i + "data"] == undefined) {
            return i;
          } else {
            if (this.list[i + "data"].snkey == undefined) {
              return i;
            }
          }
        });
        if (noMatch.length > 0) {
          var pop = {
            msg: ",!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
          return false;
        }
        // end

        // var postData = { ...this.list };
        // postData["user_snkey"] = this.$store.state.uData.snkey;
        // postData["pipeline"] = JSON.stringify(postData.pipeline);
        // postData["carryitems"] = postData.carryArray.toString(); //combobox ->string
        // // delete postData["pipelineArray"];
        // delete postData["carryArray"];
        // delete postData["hospital_name"];
        // delete postData["branch_name"];
        // delete postData["doctor_name"];
        // delete postData["main_cause_of_treatment_name"];
        // delete postData["branch"];

        // this.$store.commit("getTime"); //
        // postData["edit_man"] =
        //   this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //,,

        //
        this.$store.commit("getTime"); //
        var postData = {
          snkey: this.list.snkey,
          date: this.list.date,
          user_snkey: this.$store.state.uData.snkey,
          hospital_snkey: this.list.hospitaldata.snkey,
          branch_snkey: this.list.branchdata.snkey,
          doctor_snkey: this.list.doctordata.snkey,
          main_cause_of_treatment_snkey: this.list.main_cause_of_treatmentdata
            .snkey,
          time: this.list.time,
          outpatient: this.list.outpatient,
          description: this.list.description,
          response: this.list.response,
          back_date: this.list.back_date,
          car: this.list.car,
          accompany: this.list.accompany,
          edit_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //,,
        };

        if (this.list.pipeline != undefined) {
          postData["pipeline"] = JSON.stringify(this.list.pipeline);
        }
        if (this.list.carryArray != undefined) {
          postData["carryitems"] = this.list.carryArray.toString(); //combobox ->string
        }
        //end

        this.loading = true;
        this.axios
          .post(
            "general/edit/" +
              this.$store.state.databaseName +
              "/medical_record",
            qs.stringify(postData)
          )
          .then((rs) => {
            console.log(rs.data);
            if (rs.data.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;
              this.$emit("getMedicalData");
              this.loading = false;
            }
          });

        // -> :
        if (this.accountingCheck) {
          // alert("run");
          this.accountingFeesCheck();
        }
        //end
      } else {
        var pop = {
          msg: "!!!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //-> :
    accountingFeesCheck() {
      var carFee = { ...this.list }; //
      //-> :
      if (carFee.car != undefined && carFee.hospitaldata != undefined) {
        console.log("run car");
        //this.$store.commit("setLoadingOn"); //on
        //
        this.axios
          .post("general/getAll/" + this.$store.state.databaseName + "/fee")
          .then((rs) => {
            // console.log(carFee);
            var feeData = [];
